import React, { Component } from  'react';
const equalSign = "https://pngimg.com/uploads/equals/equals_PNG42.png";
const arrow = "https://w7.pngwing.com/pngs/780/714/png-transparent-computer-keyboard-backspace-computer-icons-arrow-computer-keyboard-rectangle-arrow.png";
let values = {};
function func(str,v)
{
    str.split(" ");
    if(str.split(" ")[1] === "USD" && str.split(" ")[3] === "SMN")
        return parseInt(str.split(" ")[0]) * (v.USDTJS);
    else
    if(str.split(" ")[1] === "SMN" && str.split(" ")[3] === "USD")
        return parseInt(str.split(" ")[0]) / v.USDTJS;
    else
    if(str.split(" ")[1] === "RUB" && str.split(" ")[3] === "SMN")
        return parseInt(str.split(" ")[0]) * (v.USDTJS / v.USDRUB);
    else
        if(str.split(" ")[1] === "SMN" && str.split(" ")[3] === "RUB")
    return parseInt(str.split(" ")[0]) / (v.USDTJS / v.USDRUB);
    else
    if(str.split(" ")[1] === "RUB" && str.split(" ")[3] === "USD")
    {
        return parseInt(str.split(" ")[0]) / (v.USDRUB);
    }
    else
    if(str.split(" ")[1] === "USD" && str.split(" ")[3] === "RUB")
    {
        return parseInt(str.split(" ")[0]) * (v.USDRUB);
    }
    else
    if(str.split(" ")[1] === "EUR" && str.split(" ")[3] === "SMN")
    {
        return parseInt(str.split(" ")[0]) * (v.USDTJS / v.USDEUR);
    }
    else 
    if(str.split(" ")[1] === "SMN" && str.split(" ")[3] === "EUR")
    {
        return parseInt(str.split(" ")[0]) / (v.USDTJS / v.USDEUR);
    }
    else
    if(str.split(" ")[1] === "EUR" && str.split(" ")[3] === "RUB")
    {
        return parseInt(str.split(" ")[0]) / (v.USDEUR / v.USDRUB);
    }
    else
    if(str.split(" ")[1] === "RUB" && str.split(" ")[3] === "EUR")
    {
        return parseInt(str.split(" ")[0]) * (v.USDEUR / v.USDRUB);
    }
    else
    if(str.split(" ")[1] === "USD" && str.split(" ")[3] === "EUR")
    {
        return parseInt(str.split(" ")[0]) * (v.USDEUR);
    }
    else
    if(str.split(" ")[1] === "EUR" && str.split(" ")[3] === "USD")
    {
        return parseInt(str.split(" ")[0]) / (v.USDEUR);
    }
}
export default class Converter extends Component {
    constructor(props)
    {
        super(props);
        this.state = {
            val: "",
            ans: 0
        }
        this.onTextChange = this.onTextChange.bind(this);
        this.onFormSubmit = this.onFormSubmit.bind(this);
    }   
    componentDidMount()
        {
            fetch("http://api.currencylayer.com/live?access_key=199fb439878056d35361096ce8cbadbd&format=1")
            .then(res => res.json())
            .then(
                (result) => {
                   // console.log(result); 
                    values = result.quotes;
                }
            )
        }
    onFormSubmit(e)
    {
        e.preventDefault();
        this.setState({
            ans: func(this.state.val,values)
        })
    }
    onTextChange(e)
    {
        this.setState({
            val: e.target.value,
        })
        this.setState({
            ans: func(this.state.val,values)
        })
    } 
    render()
    {     
       console.log(this.state.ans);
        return(
    <div>
        <form onSubmit={this.onFormSubmit}>
            <div className="main">
                <div className="main-div item1">
                    <div className="item">
                        <input onChange={this.onTextChange} type="text" name="from" className="from"/>
                    </div>
                        <div>
                        <button type="submit" className="equal-button">
                            <img className="equal-sign" src={equalSign} alt="equal"/>
                        </button>
                    </div> 
                    <div className="item">
                        <input disabled className="to" value={this.state.ans} name="to" />
                    </div> 
                </div>
            </div>
            <a href="/" className="arrow-button"><img className="arrow" src={arrow} alt="Назад"/></a>
        </form>
    </div>
        )
    }
}