import React, { Component } from 'react';
let values = {},vals = []
const arrow = "https://w7.pngwing.com/pngs/780/714/png-transparent-computer-keyboard-backspace-computer-icons-arrow-computer-keyboard-rectangle-arrow.png";
export default class Valute extends Component {
    constructor(props)
	{
		super(props);
		this.state = {
			valute: "Таджикские сомони",
		}
		this.onValuteSelect = this.onValuteSelect.bind(this);
	}
	componentDidMount()
        {
            fetch("http://api.currencylayer.com/live?access_key=199fb439878056d35361096ce8cbadbd&format=1")
            .then(res => res.json())
            .then(
                (result) => {
                   // console.log(result); 
                    values = result.quotes;
                }
            )
        }
	onValuteSelect(e)
	{
		this.setState({
			valute: e.target.value
		})
		
	}

	func()
	{
		if(this.state.valute === "Доллары США")
		{
			return <tbody>
			<tr className="fle">	
			<td>Доллары США</td>
			<td>Евро</td>
			<td>{values.USDEUR}</td>
			</tr><tr className="fle">
			<td >Доллары США</td>
			<td>Российские рубли</td>
			<td>{values.USDRUB}</td>
			</tr>
			<tr className="fle"> 			
			<td className="fle">Доллары США</td>
			<td>Таджикские сомони</td>
			<td>{values.USDTJS}</td>
			</tr>

			</tbody>			
		}
		else if(this.state.valute === "Российские рубли")
		{
			return <tbody>
			<tr className="fle">	
			<td>Российские рубли</td>
			<td>Евро</td>
			<td>{values.USDEUR / values.USDRUB}</td>
			</tr><tr className="fle">
			<td>Российские рубли</td>
			<td>Доллары США</td>
			<td>{1 / values.USDRUB}</td>
			</tr>
			<tr className="fle">
			<td>Российские рубли</td>
			<td>Таджикские сомони</td>
			<td>{(values.USDTJS / values.USDRUB)}</td>
			</tr>
			</tbody>
		}
		
		else if(this.state.valute === "Евро")
		{
			return <tbody>
			<tr className="fle">	
			<td>Евро</td>
			<td>Таджикские сомони</td>
			<td>{values.USDEUR / values.USDTJS}</td>
			</tr><tr className="fle">
			<td>Евро</td>
			<td>Российские рубли</td>
			<td>{values.USDEUR / values.USDRUB}</td>
			</tr>
			<tr className="fle">
			<td>Евро</td>
			<td>Доллары США</td>
			<td>{(1 / values.USDEUR)}</td>
			</tr>
			</tbody>
		}
			return <tbody>
			<tr className="fle">	
			<td>Таджикские сомони</td>
			<td>Евро</td>
			<td>{values.USDTJS / values.USDEUR}</td>
			</tr><tr className="fle">
			<td>Таджикские сомони</td>
			<td>Российские рубли</td>
			<td>{values.USDTJS / values.USDRUB}</td>
			</tr>
			<tr className="fle">
			<td>Таджикские сомони</td>
			<td>Доллары США</td>
			<td>{(1 / values.USDTJS)}</td>
			</tr>
			</tbody>
		
	}
	render()
    {
		console.log("val", vals)
        return(
            <div className="mm">
            <a href="/" className="arrow-button1"><img className="arrow1" src={arrow} alt="Назад"/></a>
			<br/>
			Выбор базовой валюты
					<br/>
					<select onChange={this.onValuteSelect}>
						<option>Таджикские сомони</option>
						<option>Евро</option>
						<option>Российские рубли</option>
						<option>Доллары США</option>
					</select>
				<div className="md">
					
					<table>
						<tbody>
						{this.func()}								
						</tbody>
					</table>
				</div>
			</div>       
			)
    }
}